import React, { useState } from 'react';
import {
  FaLink,
  FaQq,
  FaShareAlt
} from 'react-icons/fa';
import { toast } from 'react-hot-toast';

export interface SocialShareProps {
  /** ÂàÜ‰∫´ÁöÑÊ†áÈ¢ò */
  title: string;
  /** ÂàÜ‰∫´ÁöÑURL, ÈªòËÆ§‰∏∫ÂΩìÂâçÈ°µÈù¢URL */
  url?: string;
  /** ÂàÜ‰∫´ÁöÑÊèèËø∞ÊñáÊú¨ */
  description?: string;
  /** ÂàÜ‰∫´ÁöÑÂõæÁâáURL */
  imageUrl?: string;
  /** ÂàÜ‰∫´ÁöÑÊ†áÁ≠æ (Áî®‰∫éTwitterÁ≠â) */
  tags?: string[];
  /** ÈÄöËøáhashtagÂàóË°® (Áî®‰∫éTwitterÁ≠â) */
  hashtags?: string[];
  /** Ëá™ÂÆö‰πâÂàÜ‰∫´ÊåâÈíÆÊ†∑Âºè */
  buttonClassName?: string;
  /** ÊòæÁ§∫‰∏∫‰∏ãÊãâËèúÂçï */
  dropdown?: boolean;
  /** ÊòæÁ§∫ÁöÑÂπ≥Âè∞ÂàóË°®, ‰∏ç‰º†ÂàôÊòæÁ§∫ÊâÄÊúâÊîØÊåÅÁöÑÂπ≥Âè∞ */
  platforms?: ('twitter' | 'facebook' | 'telegram' | 'linkedin' | 'reddit' | 'weibo' | 'qq')[];
  /** Ëá™ÂÆö‰πâ‰∫ã‰ª∂Ë∑üË∏™ÂõûË∞É */
  onShare?: (platform: string) => void;
}

/**
 * Á§æ‰∫§ÂàÜ‰∫´ÁªÑ‰ª∂
 * ÊîØÊåÅÂ§öÂπ≥Âè∞ÂàÜ‰∫´ÔºåÂèØ‰ª•Ëá™ÂÆö‰πâÊ†∑ÂºèÂíåÊòæÁ§∫ÁöÑÂπ≥Âè∞
 */
const SocialShare: React.FC<SocialShareProps> = ({ 
  title,
  url = typeof window !== 'undefined' ? window.location.href : '',
  description = '',
  imageUrl = '',
  tags = [],
  hashtags = [],
  buttonClassName = 'w-10 h-10 flex items-center justify-center rounded-full transition-colors',
  dropdown = false,
  platforms,
  onShare
}) => {
  const [isOpen, setIsOpen] = useState<boolean>(false);

  // ÊûÑÂª∫TwitterÂàÜ‰∫´URL
  const getTwitterShareUrl = () => {
    const params = new URLSearchParams();
    params.append('text', `${title}\n\n${description}`);
    params.append('url', url);
    
    if (hashtags.length > 0) {
      params.append('hashtags', hashtags.join(','));
    }
    
    return `https://twitter.com/intent/tweet?${params.toString()}`;
  };

  // ÊûÑÂª∫FacebookÂàÜ‰∫´URL
  const getFacebookShareUrl = () => {
    const params = new URLSearchParams();
    params.append('u', url);
    params.append('quote', title);
    
    return `https://www.facebook.com/sharer/sharer.php?${params.toString()}`;
  };

  // ÊûÑÂª∫TelegramÂàÜ‰∫´URL
  const getTelegramShareUrl = () => {
    const params = new URLSearchParams();
    params.append('url', url);
    params.append('text', `${title}\n\n${description}`);
    
    return `https://t.me/share/url?${params.toString()}`;
  };

  // ÊûÑÂª∫LinkedInÂàÜ‰∫´URL
  const getLinkedinShareUrl = () => {
    const params = new URLSearchParams();
    params.append('url', url);
    params.append('title', title);
    params.append('summary', description);
    
    return `https://www.linkedin.com/shareArticle?mini=true&${params.toString()}`;
  };

  // ÊûÑÂª∫RedditÂàÜ‰∫´URL
  const getRedditShareUrl = () => {
    const params = new URLSearchParams();
    params.append('url', url);
    params.append('title', title);
    
    return `https://www.reddit.com/submit?${params.toString()}`;
  };

  // ÊûÑÂª∫ÂæÆÂçöÂàÜ‰∫´URL
  const getWeiboShareUrl = () => {
    const params = new URLSearchParams();
    params.append('url', url);
    params.append('title', `${title}\n\n${description}`);
    
    if (imageUrl) {
      params.append('pic', imageUrl);
    }
    
    return `https://service.weibo.com/share/share.php?${params.toString()}`;
  };

  // ÊûÑÂª∫QQÂàÜ‰∫´URL
  const getQQShareUrl = () => {
    const params = new URLSearchParams();
    params.append('url', url);
    params.append('title', title);
    params.append('desc', description);
    
    if (imageUrl) {
      params.append('pics', imageUrl);
    }
    
    return `https://connect.qq.com/widget/shareqq/index.html?${params.toString()}`;
  };

  // Â§çÂà∂ÈìæÊé•Âà∞Ââ™Ë¥¥Êùø
  const copyToClipboard = () => {
    navigator.clipboard.writeText(url)
      .then(() => {
        toast.success('ÈìæÊé•Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø');
      })
      .catch(() => {
        toast.error('Â§çÂà∂Â§±Ë¥•ÔºåËØ∑ÊâãÂä®Â§çÂà∂');
      });

    // Ëß¶ÂèëÂàÜ‰∫´‰∫ã‰ª∂
    onShare?.('copy');
  };

  // Â§ÑÁêÜÂàÜ‰∫´ÁÇπÂáª‰∫ã‰ª∂
  const handleShareClick = (platform: string, shareUrl: string) => {
    // ÊâìÂºÄÂàÜ‰∫´Á™óÂè£
    window.open(shareUrl, '_blank', 'width=600,height=600');
    
    // Ëß¶ÂèëÂàÜ‰∫´‰∫ã‰ª∂Ë∑üË∏™
    onShare?.(platform);
    
    // Â¶ÇÊûúÊòØ‰∏ãÊãâËèúÂçïÔºåÂÖ≥Èó≠ËèúÂçï
    if (dropdown) {
      setIsOpen(false);
    }
  };

  // ÂÆö‰πâÊîØÊåÅÁöÑÂπ≥Âè∞ÂàóË°®
  const allPlatforms = [
    {
      id: 'twitter',
      name: 'Twitter',
      icon: "üê¶",
      color: 'hover:text-blue-400 hover:bg-blue-400/10',
      getUrl: getTwitterShareUrl
    },
    {
      id: 'facebook',
      name: 'Facebook',
      icon: "üìò",
      color: 'hover:text-blue-600 hover:bg-blue-600/10',
      getUrl: getFacebookShareUrl
    },
    {
      id: 'telegram',
      name: 'Telegram',
      icon: "üì±",
      color: 'hover:text-blue-500 hover:bg-blue-500/10',
      getUrl: getTelegramShareUrl
    },
    {
      id: 'linkedin',
      name: 'LinkedIn',
      icon: "üíº",
      color: 'hover:text-blue-700 hover:bg-blue-700/10',
      getUrl: getLinkedinShareUrl
    },
    {
      id: 'reddit',
      name: 'Reddit',
      icon: "üî¥",
      color: 'hover:text-orange-600 hover:bg-orange-600/10',
      getUrl: getRedditShareUrl
    },
    {
      id: 'weibo',
      name: 'ÂæÆÂçö',
      icon: "üåê",
      color: 'hover:text-red-600 hover:bg-red-600/10',
      getUrl: getWeiboShareUrl
    },
    { 
      id: 'qq', 
      name: 'QQ', 
      icon: <FaQq />, 
      color: 'hover:text-blue-500 hover:bg-blue-500/10',
      getUrl: getQQShareUrl 
    }
  ];

  // Ê†πÊçÆ‰º†ÂÖ•ÁöÑplatformsÁ≠õÈÄâË¶ÅÊòæÁ§∫ÁöÑÂπ≥Âè∞
  const filteredPlatforms = platforms 
    ? allPlatforms.filter(p => platforms.includes(p.id as any)) 
    : allPlatforms;

  // Ê∏≤ÊüìÊôÆÈÄöÊ®°ÂºèÁöÑÂàÜ‰∫´ÊåâÈíÆÂàóË°®
  if (!dropdown) {
    return (
      <div className="flex flex-wrap gap-2">
        {filteredPlatforms.map(platform => (
          <button
            key={platform.id}
            className={`${buttonClassName} ${platform.color}`}
            onClick={() => handleShareClick(platform.id, platform.getUrl())}
            aria-label={`ÂàÜ‰∫´Âà∞${platform.name}`}
            title={`ÂàÜ‰∫´Âà∞${platform.name}`}
          >
            {platform.icon}
          </button>
        ))}
        <button
          className={`${buttonClassName} hover:text-gray-300 hover:bg-gray-300/10`}
          onClick={copyToClipboard}
          aria-label="Â§çÂà∂ÈìæÊé•"
          title="Â§çÂà∂ÈìæÊé•"
        >
          <FaLink />
        </button>
      </div>
    );
  }

  // Ê∏≤Êüì‰∏ãÊãâËèúÂçïÊ®°Âºè
  return (
    <div className="relative">
      <button
        className={`${buttonClassName} hover:text-blue-500 hover:bg-blue-500/10`}
        onClick={() => setIsOpen(!isOpen)}
        aria-label="ÂàÜ‰∫´"
        title="ÂàÜ‰∫´"
      >
        <FaShareAlt />
      </button>
      
      {isOpen && (
        <>
          {/* ÁÇπÂáªÂ§ñÈÉ®ÂÖ≥Èó≠‰∏ãÊãâËèúÂçïÁöÑÈÅÆÁΩ© */}
          <div 
            className="fixed inset-0 z-10" 
            onClick={() => setIsOpen(false)}
          />
          
          {/* ‰∏ãÊãâËèúÂçïÂÜÖÂÆπ */}
          <div className="absolute right-0 mt-2 py-2 w-48 bg-gray-800 rounded-md shadow-xl z-20 border border-gray-700">
            {filteredPlatforms.map(platform => (
              <button
                key={platform.id}
                className={`w-full text-left px-4 py-2 flex items-center gap-3 hover:bg-gray-700 transition-colors`}
                onClick={() => handleShareClick(platform.id, platform.getUrl())}
              >
                <span className={platform.color.split(' ')[0]}>
                  {platform.icon}
                </span>
                <span>{platform.name}</span>
              </button>
            ))}
            <button
              className="w-full text-left px-4 py-2 flex items-center gap-3 hover:bg-gray-700 transition-colors"
              onClick={copyToClipboard}
            >
              <span className="text-gray-400">
                <FaLink />
              </span>
              <span>Â§çÂà∂ÈìæÊé•</span>
            </button>
          </div>
        </>
      )}
    </div>
  );
};

export default SocialShare; 